/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import {
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  FormControlLabel,
  Radio,
  RadioGroup
} from '@mui/material';
import * as React from 'react';
import { useState } from 'react';
import { RepresentationDto } from '../../../../domain/organisme';
import { TabPanel, TabsContainer } from '../../../base-component/TabsContainer';
import { TextInput } from '../../../base-component/TextInput';
import { classes } from '../../../category/EditCategoriesComponent';
import { dialogClasses } from '../dialog-common';

const RepresentationPanel = (props: {
  representation: RepresentationDto;
  onClose: () => void;
}) => (
  <React.Fragment>
    <div css={dialogClasses.editBlock}>
      <h3>Reprsentant</h3>
      <TextInput name={'date'} type={'date'} label={'Date de dbut'} />
    </div>
    <div css={dialogClasses.editBlock}>
      <h3>Supplant</h3>
      {props.representation.suppleance && (
        <TextInput name={'date'} type={'date'} label={'Date de dbut'} />
      )}
      {!props.representation.suppleance && (
        <Button variant="contained">Ajouter Supplant</Button>
      )}
    </div>
    <div
      css={css`
        ${classes.editButton}
        padding-top: 40px;
      `}
    >
      <Button onClick={props.onClose} color="primary">
        Annuler
      </Button>
      <Button type="submit" variant="contained" color="primary" size="small">
        Enregistrer
      </Button>
    </div>
  </React.Fragment>
);

type SuppleantDecision = 'remplace' | 'demissionne';

const DemissionPanel = (props: {
  representation: RepresentationDto;
  onClose: () => void;
}) => {
  const [suppleantDecision, setSuppleantDecision] =
    useState<SuppleantDecision>('remplace');
  return (
    <React.Fragment>
      <div css={dialogClasses.editBlock}>
        <h3>Reprsentant</h3>
        <TextInput name={'date'} type={'date'} label={'Date de fin'} />
      </div>
      {props.representation.suppleance && (
        <div css={dialogClasses.editBlock}>
          <h3>Supplant</h3>
          <RadioGroup
            value={suppleantDecision}
            name="radio-buttons-group"
            onChange={(
              event: React.ChangeEvent<HTMLInputElement>,
              value: string
            ) => setSuppleantDecision(value as SuppleantDecision)}
          >
            <FormControlLabel
              value="remplace"
              control={<Radio />}
              label="Remplace le reprsentant  date + 1"
            />
            <FormControlLabel
              value="demissionne"
              control={<Radio />}
              label="Dmissionne  date"
            />
          </RadioGroup>
        </div>
      )}
      <div css={classes.editButton}>
        <Button onClick={props.onClose} color="primary">
          Annuler
        </Button>
        <Button type="submit" variant="contained" color="primary" size="small">
          Enregistrer
        </Button>
      </div>
    </React.Fragment>
  );
};

const DemisssionSuppleantPanel = (props: { onClose: () => void }) => (
  <React.Fragment>
    <div css={dialogClasses.editBlock}>
      <h3>Supplant</h3>
      <TextInput name={'date'} type={'date'} label={'Date de fin'} />
    </div>
    <div
      css={css`
        ${classes.editButton}
        padding-top: 40px;
      `}
    >
      <Button onClick={props.onClose} color="primary">
        Annuler
      </Button>
      <Button type="submit" variant="contained" color="primary" size="small">
        Enregistrer
      </Button>
    </div>
  </React.Fragment>
);

const SuppressionPanel = (props: {
  representation: RepresentationDto;
  onClose: () => void;
}) => (
  <React.Fragment>
    <div css={dialogClasses.editBlock}>
      La suppression est dfinitive et sert  corriger les erreurs de saisie
      uniquement.
    </div>
    {!props.representation.suppleance && (
      <div css={dialogClasses.editBlock}>
        <Button variant="contained" color="error">
          Supprimer la reprsentation
        </Button>
      </div>
    )}
    {props.representation.suppleance && (
      <React.Fragment>
        <div css={dialogClasses.editBlock}>
          <h3>Reprsentant</h3>
          Supprimer la reprsentation
          <React.Fragment>
            {' '}
            <b>et</b> la supplance
          </React.Fragment>
          <div
            css={css`
              padding: 10px 0;
            `}
          >
            <Button variant="contained" color="error">
              Supprimer
            </Button>
          </div>
        </div>
        <div css={dialogClasses.editBlock}>
          <h3>Supplant</h3>
          Supprimer la supplance uniquement
          <div
            css={css`
              padding: 10px 0;
            `}
          >
            <Button variant="contained" color="error">
              Supprimer
            </Button>
          </div>
        </div>
      </React.Fragment>
    )}
    <div
      css={css`
        ${classes.editButton}
      `}
    >
      <Button onClick={props.onClose} color="primary">
        Annuler
      </Button>
    </div>
  </React.Fragment>
);

export const EditRepresentationDiaglog = (props: {
  representation: RepresentationDto;
  display: boolean;
  onClose: () => void;
}) => {
  return (
    <Dialog
      open={props.display}
      onClose={props.onClose}
      fullWidth={true}
      maxWidth={'lg'}
    >
      <DialogTitle>Modifier</DialogTitle>
      <DialogContent>
        <TabsContainer>
          <TabPanel label="Dates de dbut">
            <RepresentationPanel
              representation={props.representation}
              onClose={props.onClose}
            />
          </TabPanel>
          <TabPanel label="Dmission reprsentant">
            <DemissionPanel
              representation={props.representation}
              onClose={props.onClose}
            />
          </TabPanel>
          {props.representation.suppleance && (
            <TabPanel label="Dmission supplant">
              <DemisssionSuppleantPanel onClose={props.onClose} />
            </TabPanel>
          )}
          <TabPanel label="Suppression">
            <SuppressionPanel
              representation={props.representation}
              onClose={props.onClose}
            />
          </TabPanel>
        </TabsContainer>
      </DialogContent>
      {/*<DialogActions>*/}
      {/*<Button onClick={props.onClose} color="primary">*/}
      {/*  Annuler*/}
      {/*</Button>*/}
      {/*<Button color="primary">Enregistrer</Button>*/}
      {/*</DialogActions>*/}
    </Dialog>
  );
};
